@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;0,800;1,400&display=swap')
*
    margin: 0
    padding: 0
table
    border-collapse: collapse
    line-height: 20px
    margin: auto
    font-size: 14px
    // text-align: center
    font-family: 'Nunito', sans-serif
    color: #222
    tr
        border-bottom: 1px solid #eee
            // display: flex

    thead
        opacity: 0.3
        tr
            background-color: #fff
    tbody
        tr
            &:nth-child(odd)
                background-color: #fafafa
            td
                padding: 10px
                &.status
                    color: #aaa
                &.red
                    animation: 2s infinite red
                &.win
                    color: #18a718
                &.lose
                    color: #fc5d5d
                &.vs
                    opacity: 0.3
                    padding: 0
                span
                    display: block
                    font-size: 12px
                    text-align: center
                    // border: 1px solid #aaa
                    border-radius: 20px
                    line-height: 16px
                    opacity: 0.5
                    // color: #5d

@keyframes red
    0%
        color: #aaa
    50%
        color: #fc5d5d
    100%
        color: #aaa